import React, {useCallback} from 'react';

import {Heading, Body} from 'react-shared/components/Text';
import {Spacer} from 'react-shared/components/Spacer';
import {VSCodeButton} from '@vscode/webview-ui-toolkit/react';
import {vscode} from '~webviews/lighthouse.entry';
import {Icon} from 'react-shared/components/Icon';
import {sendToExtension} from '~common/messaging/modules/lighthouse';

export function ErrorScreen() {
  const retry = useCallback(() => sendToExtension(vscode, {type: 'resetLighthouse'}), []);

  return (
    <>
      <Icon icon="warning" style={{fontSize: 48, color: 'var(--vscode-warning-foreground)'}} />
      <Spacer size="xxlarge" />
      <Heading level={2}>Error while running Lighthouse</Heading>
      <Spacer size="medium" />
      <Body secondary>See terminal task output to view logs</Body>
      <Spacer size="xlarge" />
      <VSCodeButton onClick={retry}>Retry</VSCodeButton>
    </>
  );
}
