import React, {useEffect, useState, useMemo} from 'react';
import {createRoot} from 'react-dom/client';

import styles from './lighthouse.entry.scss';
import {setupVsCodeHelperIframe} from './mainpanel-vscode-helper';
import {provideVSCodeDesignSystem} from '@vscode/webview-ui-toolkit';
import {StartScreen} from './components/lighthouse/StartScreen';
import {LoadingScreen} from './components/lighthouse/LoadingScreen';
import {Webview} from 'vscode';
import {ErrorScreen} from './components/lighthouse/ErrorScreen';
import {
  ExtensionToWebviewMessage,
  extensionToWebviewSchema,
} from '~common/messaging/modules/lighthouse';

provideVSCodeDesignSystem(document.documentElement);

declare global {
  interface Window {
    previewRoute: string;
    acquireVsCodeApi: () => Webview;
  }
}

export const vscode = window.acquireVsCodeApi();
setupVsCodeHelperIframe(vscode);

const enum Status {
  OK,
  LOADING,
  ERROR,
}

function App() {
  const [status, setStatus] = useState<Status>(Status.OK);

  useEffect(() => {
    window.addEventListener('message', handleEvent);
    async function handleEvent({data}: {data: string}) {
      const result = extensionToWebviewSchema.safeParse(data);
      if (!result.success) {
        console.error('Invalid message received', result.error);
        return;
      }

      const message: ExtensionToWebviewMessage = result.data;

      switch (message) {
        case 'showLoadingState':
          setStatus(Status.LOADING);
          break;
        case 'showErrorState':
          setStatus(Status.ERROR);
          break;
      }
    }

    return () => window.removeEventListener('message', handleEvent);
  }, []);

  const Slot = useMemo(() => {
    switch (status) {
      case Status.OK:
        return StartScreen;
      case Status.LOADING:
        return LoadingScreen;
      case Status.ERROR:
        return ErrorScreen;
    }
  }, [status]);

  return (
    <div className={styles.root}>
      <Slot />
    </div>
  );
}

const root = createRoot(document.getElementById('root')!);
root.render(<App />);
