import React, {useState, useCallback} from 'react';

import styles from '../..//lighthouse.entry.scss';
import {Heading} from 'react-shared/components/Text';
import {Spacer} from 'react-shared/components/Spacer';
import {VSCodeButton, VSCodeCheckbox} from '@vscode/webview-ui-toolkit/react';
import {LighthouseLogo} from './lighthouse.svg';
import {vscode} from '~webviews/lighthouse.entry';
import {sendToExtension} from '~common/messaging/modules/lighthouse';

const categories = {
  performance: 'Performance',
  accessibility: 'Accessibility',
  'best-practices': 'Best Practices',
  seo: 'SEO',
  pwa: 'Progressive Web App',
};
type Audit = keyof typeof categories;

const audits = Object.keys(categories) as Audit[];

export function StartScreen() {
  const [auditsToRun, setAuditsToRun] = useState(audits);

  const toggleAudit = (e: Event | React.FormEvent<HTMLElement>) => {
    const checkbox = e.currentTarget as HTMLInputElement;
    const value = checkbox.value as Audit;

    setAuditsToRun(old => {
      const index = old.indexOf(value);
      if (index === -1) {
        return [...old, value];
      } else {
        return old.filter(item => item !== value);
      }
    });
  };

  const startAudit = useCallback(
    () => sendToExtension(vscode, {type: 'startLighthouseAudit', auditsToRun}),
    [auditsToRun]
  );

  return (
    <>
      <LighthouseLogo />
      <Spacer size="xlarge" />

      <Heading level={2}>Generate a Lighthouse report</Heading>
      <Spacer size="xlarge" />

      {window.previewRoute && window.previewRoute !== '/' && (
        <>
          <div className={styles.pathname}>
            <span>Path</span>
            <pre>{window.previewRoute}</pre>
          </div>
          <Spacer size="large" />
        </>
      )}

      <div className={styles.checkboxList}>
        {audits.map((audit, i) => (
          <VSCodeCheckbox
            key={audit}
            checked={auditsToRun.includes(audit)}
            onChange={toggleAudit}
            value={audit}
          >
            {categories[audit]}
          </VSCodeCheckbox>
        ))}
      </div>

      <Spacer size="xxlarge" />
      <VSCodeButton onClick={startAudit}>Analyze Page</VSCodeButton>
    </>
  );
}
